.username{
  color: #FF5524;
  cursor: pointer;
  position: relative;
  font-size:1.5rem;
  border:0.5px solid rgba(255,100,100,0.5);
  border-radius:10px;
  padding:5px 10px;
}

.username.copied::before{
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  content: 'Copied to clipboard';
  background: grey;
  box-shadow: 0 1px 4px rgba(0,0,0,0.1),0 4px 12px rgba(0,0,0,0.06);
  box-shadow: var(--lift-shadow);
  padding: 0.25rem;
  padding: var(--spacing-unit-xmicro);
  border-radius: 0.3535534rem;
  border-radius: var(--spacing-unit-micro);
  font-size: 0.8rem;
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  color:white;
}

.username.copied::after{
  content: '';
  background: grey;
  padding: 0.25rem;
  padding: var(--spacing-unit-xmicro);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  transform: rotate(45deg);
}

.callBox, .disclaimer{
  margin-bottom: 1.0000001064rem;
  margin-bottom: var(--spacing-unit);
}

.form-input{
  padding: 1.0000001064rem 1.4142137505rem;
  padding: var(--spacing-unit) var(--spacing-unit-large);
  width: 80%;
  margin-top:20px;
  border: 3px solid rgba(228,232,235,1);
  border: 3px solid rgba(var(--color-input-border),1);
  border-radius: calc(0.25rem*2);
  border-radius: calc(var(--rounded-radius)*2);
  font-weight: 500;
  font-weight: var(--font-weight-text);
  line-height: 1.2;
  background-color: rgba(255,255,255,1);
  background-color: rgba(var(--color-background),1);
  color: rgba(1, 41, 46,1);
  color: rgba(var(--color-text),1);
  transition: box-shadow .2s cubic-bezier(.4,0,.2,1);
  outline: none;
  margin-right: 1.4142137505rem;
  margin-right: var(--spacing-unit-large);
}

.form-input::placeholder, .form-input:-moz-placeholder{
  font-weight: 500;
  font-weight: var(--font-weight-text);
  line-height: 1.2;
  color: rgba(1, 41, 46,1);
  color: rgba(var(--color-text),1);
}

.form-input:hover {
  box-shadow: 0 1px 4px #0000001a, 0 4px 12px rgba(0,0,0,.06);
  box-shadow: 0 1px 4px rgba(0,0,0,0.1),0 4px 12px rgba(0,0,0,0.06);
  box-shadow: var(--lift-shadow);
}

.form-input:focus{
  border: 3px solid #F05524;
  box-shadow: 0 1px 4px #0000001a, 0 4px 12px rgba(0,0,0,.06);
  box-shadow: 0 1px 4px rgba(0,0,0,0.1),0 4px 12px rgba(0,0,0,0.06);
  box-shadow: var(--lift-shadow);
}

.highlight {
