}

.primaryButton{
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 1.15rem;
  font-weight: 600;
  font-weight: var(--font-weight-semibold);
  line-height: 1.25;
  padding: 0.7071068376rem;
  padding: var(--spacing-unit-small);
  background-color: #FD913C;
  background-color: var(--secondary-color);
  color: white;
  width:80%;
  margin:10px 10%;
  border-radius: .25rem;
  border-radius: 0.25rem;
  border-radius: var(--rounded-radius);
  transition: background-color .2s cubic-bezier(.4,0,.2,1),color .2s cubic-bezier(.4,0,.2,1),box-shadow .2s cubic-bezier(.4,0,.2,1);
}

.primaryButton:hover, .primaryButton:active{
  box-shadow: 0 1px 4px rgba(0,0,0,0.1),0 4px 12px rgba(0,0,0,0.06);
  box-shadow: var(--lift-shadow);
}

.primaryButton:hover{
  background-color: #ffe082;
  background-color: var(--secondary-color-hover);
}

.primaryButton:active{
  background-color: #ffecb3;
  background-color: var(--secondary-color-active);
}

.alertButtonPrimary,.alertButtonSecondary{
  border: none;
  outline: none;
  width: 4rem;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  font-weight: var(--font-weight-semibold);
  line-height: 1.25;
  padding: 0.25rem;
  padding: var(--spacing-unit-xmicro);
  color: #1f292e;
  border-radius: .25rem;
  border-radius: 0.25rem;
  border-radius: var(--rounded-radius);
  transition: background-color .2s cubic-bezier(.4,0,.2,1),color .2s cubic-bezier(.4,0,.2,1),box-shadow .2s cubic-bezier(.4,0,.2,1);
}

.alertButtonPrimary{
  background-color: #26c6da;
  background-color: var(--primary-color);
}

.alertButtonSecondary{
  background-color: #f44336;
  background-color: var(--warning-color);
}

.alertButtonPrimary:hover, .alertButtonPrimary:active, .alertButtonSecondary:hover, .alertButtonSecondary:active{
  box-shadow: 0 1px 4px rgba(0,0,0,0.1),0 4px 12px rgba(0,0,0,0.06);
  box-shadow: var(--lift-shadow);
}

.alertButtonPrimary:hover{
  background-color: rgba(13,163,182,0.08);
  background-color: var(--primary-color-hover);
}

.alertButtonSecondary:hover{
  background-color: #ffcdd2;
  background-color: var(--warning-color-hover);
}

.alertButtonPrimary:active{
  background-color: rgba(13,163,182,0.16);
  background-color: var(--primary-color-active);
}

.alertButtonSecondary:active{
  background-color: #ef9a9a;
  background-color: var(--warning-color-active);
}


.incomingCallContainer{
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  transition: display .2s cubic-bezier(.4,0,.2,1);
}

.incomingCall{
  height: auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  background-color: #fff;
  border-radius: calc(0.25rem*2);
  border-radius: calc(var(--rounded-radius)*2);
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 1px 4px rgba(0,0,0,0.1),0 4px 12px rgba(0,0,0,0.06);
  box-shadow: var(--lift-shadow);
  padding: 1.0000001064rem;
  padding: var(--site-margin);
  justify-content: space-between;
  -webkit-animation: callAnimation 2s infinite ease-in-out;
          animation: callAnimation 2s infinite ease-in-out;
}

@-webkit-keyframes callAnimation {
  0% {
    transform: translate(-50%,-50%) scale(1) ;
  }
  50% {
    transform: translate(-50%,-50%) scale(1.1);
  }
  100% {
    transform: translate(-50%,-50%) scale(1);
  }
}

@keyframes callAnimation {
  0% {
    transform: translate(-50%,-50%) scale(1) ;
  }
  50% {
    transform: translate(-50%,-50%) scale(1.1);
  }
  100% {
    transform: translate(-50%,-50%) scale(1);
  }
}

