body { margin: 0; padding-bottom: 3rem; font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif; }
#form { background: rgba(0, 0, 0, 0.15); padding: 0.25rem; position: fixed; bottom: 0; left: 0; right: 0; display: flex; height: 3rem; box-sizing: border-box; -webkit-backdrop-filter: blur(10px); backdrop-filter: blur(10px); }
#input { border: none; padding: 0 1rem; flex-grow: 1; border-radius: 2rem; margin: 0.25rem; }
#input:focus { outline: none; }
#form > button { background: #333; border: none; padding: 0 1rem; margin: 0.25rem; border-radius: 3px; outline: none; color: #fff; }
.messages-section { list-style-type: none; margin: 0; padding: 0; }
.messages-section > li {
     padding: 0.5rem 1rem; 
     margin:10px 0px;
}
.messages-section > li:nth-child(odd) { 
    background: #F13AB1; 
    color:white;
    text-align:right; 
    margin-left:25px; 
    border-bottom-left-radius:15px;
}
.messages-section> li:nth-child(even){
    background: #efefef; 
    text-align:left; 
    margin-right:25px; 
    border-bottom-right-radius:15px;
}

.chat-box{
    background:white;
    width:47%;
    height:44%;
    border:1px solid rgba(0,0,0,0.3);
    margin-top:64px;
    text-align:center;
    padding:5px;
    box-shadow:2px 2px 5px 2px rgba(0,0,0,0.1);
    position:absolute;
    bottom:0;
    right:0;
}

.messages-section{
    text-align:left;
    overflow:auto;
    height:90%;
}
.chat-box-input{
    padding:10px;
    border-radius:5px;
    outline:none;
    border:1px solid grey;
    width:70%;
}

.chat-input-section{
    position:absolute;
    bottom:0;
    right:0;
    margin:10px 0px;
    width:100%;
}

.send-message{
    padding:10px;
    margin:0px 10px;
    border-radius:5px;
    border:none;
    box-shadow:2px 2px 5px 2px rgba(0,0,0,0.3);
    color:white;
    background-color: rgba(var(--color-dark-background),1);
}
.send-message:hover{
    cursor:pointer;
}
.send-message:focus{
    outline:none;
    border:none;
    /* box-shadow:none; */
}
.send-messages:active{
    box-shadow:none;
}



.footerWrapper{
    width: 100%;
    background-color: rgba(var(--color-dark-background),1);
    text-align: center;
    color: var(--grey-1);
    padding: 20px;
  }
  
  .footerContainer{
